import { Component } from 'react'
import PropTypes from 'prop-types'
import { withRouter } from 'next/router'
import getConfig from 'next/config'
import List, {
  ListItem,
  ListItemGraphic,
  ListItemText,
  ListItemMeta,
} from '@material/react-list'
import MaterialIcon from '@material/react-material-icon'

const { publicRuntimeConfig } = getConfig()
const { HOME_ROUTE } = publicRuntimeConfig

const NAV_LINKS = [
  {
    path: '/',
    icon: 'home',
    label: 'Home',
  },
  {
    path: '/search',
    icon: 'search',
    label: 'Search',
  },
  {
    path: '/settings',
    icon: 'settings',
    label: 'Settings',
    meta: 'meta info',
  },
]

class DrawerList extends Component {
  componentDidMount() {
    const { router } = this.props
    // NAV_LINKS.forEach(x => router.prefetch(x.path)) // do not include HOME_ROUTE prefix
  }
  render() {
    const { onItemClick, router } = this.props
    const selectedIndex = NAV_LINKS.reduce((agg, x, i) => (
      x.path === router.pathname ? i : agg
    ), 0)
    return (
      <List
        tag='nav'
        singleSelection
        selectedIndex={selectedIndex}
      >
        {NAV_LINKS.map(({ path, icon, label, meta }) => (
          <ListItem
            tag='a'
            key={path}
            href={`${HOME_ROUTE}${path}`}
            onClick={e => {
              e.preventDefault()
              router.push(`${HOME_ROUTE}${path}`)
              onItemClick()
            }}
          >
            <ListItemGraphic graphic={<MaterialIcon icon={icon}/>} />
            <ListItemText primaryText={label} />
            <ListItemMeta meta={meta || ''} />
          </ListItem>
        ))}
      </List>
    )
  }
}
DrawerList.propTypes = {
  onItemClick: PropTypes.func.isRequired,
}

export default withRouter(DrawerList)
